const parabola = (params = {}) => {
  let startPos = params.startPos, //
    endPos = params.endPos, //
    callback = params.callback, //
    frame = params.frame, //

    x1 = startPos.x,
    x2 = endPos.x,
    y1 = startPos.y,
    y2 = endPos.y,

    /**
     * (x1, y1) -    (x2, y2) - 
     * (0, 0), 
     * , (0, 0),  (x2 - x1, y2 - y1), web, ,a
     * : y = (endY / Math.pow(endX, 2)) * x * x
     */
    endX = x2 - x1,
    endY = y2 - y1,

    g = 0.4, // ()
    s = 18, // ()

    /**
     * 
     * 0 -  <   y,x, 
     * 1 -  >   x,y, 
     */
    mode = Math.abs(endX) < Math.abs(endY) ? 0 : 1,

    // 'L'    'R'    'D'   'U'  
    directionX = endX > 0 ? 'R' : endX < 0 ? 'L' : '',
    directionY = endY > 0 ? 'D' : endY < 0 ? 'U' : '',

    a = endY / Math.pow(endX, 2),

    x = 0,
    y = 0,
    tick = 0;

  const raf = (fn) => { setTimeout(fn, 1000 / 52); };

  const run = () => {
    tick++;

    if (mode == 0) {
      y = g * tick * tick; // : 
      x = Math.sqrt(Math.abs(y / a)); // , y, 
    } else {
      x = Math.abs(x) + s;
      y = Math.abs(a) * x * x;
    }

    if (directionX == 'L') x = -x;
    if (directionY == 'U') y = -y;

    //
    frame(x + x1, y + y1);

    /* y */
    if (directionY == 'D' && y < endY || directionY == 'U' && y > endY) {
      raf(run);
    } else {
      if (typeof callback === 'function') {
        callback();
      }
    }
  };

  raf(run);
};

module.exports = parabola;



